import Vue from 'vue';
import { Store } from 'vuex';

import { closeDepartmentSelection, t } from './lib';

const state = (multipleSelectionMode, allowEmpty, style) => ({
  departments: {},
  selectedDepartmentIds: [],
  multipleSelectionMode,
  allowEmpty,
  style: { ...style },
  error: null,
});
const getters = (filter) => ({
  sortedDepartments: state => {
    return Object.values(state.departments)
      .filter(filter)
      .sort((g1, g2) =>
        g1.NAME.toLowerCase() < g2.NAME.toLowerCase()
          ? -1
          : Number(g1.NAME.toLowerCase() > g2.NAME.toLowerCase()));
  },
  selectedDepartments: state =>
    state.selectedDepartmentIds.map(id => state.departments[id]),
  selectedDepartmentsOrDepartment: state =>
    selectedDepartmentsOrDepartment(state),
});

const selectedDepartmentsOrDepartment =
  ({ departments, selectedDepartmentIds, multipleSelectionMode }) =>
    multipleSelectionMode
      ? [ ...selectedDepartmentIds.map(id => departments[id]) ]
      : selectedDepartmentIds.length === 1
        ? { ...departments[selectedDepartmentIds[0]] }
        : null;

const isEmptySelection = ({ selectedDepartmentIds }) =>
  selectedDepartmentIds.length === 0;

const arrEq = (a1, a2) => {
  if (a1.length !== a2.length) { return false; }
  return a1.every((item, ix) => item === a2[ix]);
};

const mutations = {
  setSelection: (state, { model }) => {
    // model -    1 
    const selection = state.selectedDepartmentIds;
    if (state.multipleSelectionMode) {
      const newSelection = model.map(u => u.ID);
      // condition to prevent watchers from endless cycle
      if (arrEq(selection, newSelection)) { return; }
      state.selectedDepartmentIds = newSelection;
    } else {
      if (model === null) {
        // condition to prevent watchers from endless cycle
        if (selection.length !== 0) {
          state.selectedDepartmentIds = [];
        }
      } else {
        const ID = model.ID;
        // condition to prevent watchers from endless cycle
        if (selection.length === 1 && selection[0] === ID) { return; }
        state.selectedDepartmentIds = [ ID ];
      }
    }
    state.error = null;
  },
  setModelFromArray(state, { departmentIds }) {
    this.commit('setSelection', {
      model: state.multipleSelectionMode
        ? departmentIds
          .filter(uid => state.departments.hasOwnProperty(uid))
          .map(uid => ({ID: uid}))
        : departmentIds.length !== 0
          ? {ID: departmentIds[0]}
          : null,
    });
  },
  addDepartments: (state, departments) => {
    Vue.set(state, 'departments', departments.reduce((map, department) => {
      map[department.ID] = department;
      return map;
    }, { ...state.departments }));
  },
  onError(state, { message, error = null }) {
    state.error = { message, error };
  },
};

const actions = (selectDepartmentCallback, initiallySelected) => ({
  getDepartments: ({ commit }) => {
    const onSuccess = departments => {
      commit('addDepartments', departments);
      if (initiallySelected.length) {
        commit('setModelFromArray', { departmentIds: initiallySelected });
      }
    };
    const onError = error => {
      commit('onError', {
        error,
        message: t('message.error_fetching_departments_list'),
      });
    };
    const departments = [];
    window.BX24.callMethod('department.get', {}, result => {
      let error;
      // eslint-disable-next-line no-cond-assign
      if (error = result.error()) {
        onError(error);
      } else {
        departments.push(...result.data());
        if (result.more()) {
          result.next();
        } else {
          onSuccess(departments);
        }
      }
    });
  },
  applyDepartmentSelection: ({ state, commit }) => {
    if (!state.allowEmpty && isEmptySelection(state)) {
      commit('onError', { message: t('message.select_department') });
      return;
    }
    closeDepartmentSelection();
    selectDepartmentCallback(selectedDepartmentsOrDepartment(state));
  },
  resetAndClose: ({ state, commit }) => {
    commit('setSelection', {
      model: state.multipleSelectionMode ? [] : null,
    });
    closeDepartmentSelection();
  },
});


export const store = (
  selectDepartmentCallback, multiple,
  allowEmpty, initiallySelected, filter, style,
) => new Store({
  state: state(multiple, allowEmpty, style),
  getters: getters(filter),
  mutations,
  actions: actions(selectDepartmentCallback, initiallySelected),
});
